*{
    margin: 0;
    padding: 0; 
}
body
{
    background: url(https://img.freepik.com/premium-vector/quiz-abstract-background_255524-114.jpg);
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-position: initial;
}
.heading {
    text-align: center;
    color: #f71a89;
}
.heading:hover{
    cursor: pointer;
    color:blue
}
.app{
    background:linear-gradient(83deg,#e68f1d 0% #4853ea);
    width: 100%;
    min-height: 200px;
    height: min-content;
    border-radius: 30px;
    padding: 20px;
    box-shadow: 10px 10px 42px rgb(118, 115, 115);  
    display: flex;
    justify-content: space-between;
}
.Question-Section{
    position: relative;
    width: 100%;
    color: white;
    }
.Question-Section h1 span{
    color: rgba(46, 204, 236, 0.963);
    font-size: 40px;

}
.Question-Text{
    margin-top: 30px;
    font-size: 25px;
    color: chartreuse;
}
.Answer-Section{
    display: flex;
    flex-direction: column;
    width: 50%;
    justify-content: space-around;
}
button{
    width: 100%;
    font-size: 18px;
    color: black;
    border-radius: 20px;
    padding: 10px;
    border: 3px solid blueviolet;
}
button:hover{
    color: blue;
    cursor: pointer;
}
.score-sec{
    display: flex;
    color: white;
    align-items: center;
    font-size: 25px;
    /* padding-top: 70px; */
}